  @media (min-width: 1280px) {
    .content, footer {
      width: 1280px;
      margin-left: auto;
      margin-right: auto; } }

.clear-both {
  clear: both; }

footer {
  display: flex;
  margin-top: 47px;
  margin-bottom: 57px; }
  footer a {
    text-decoration: none; }
  footer .with-love {
    margin-top: 0;
    font-size: 19px; }
    @media (max-width: 424px) {
