        display: none; } }

nav {
  display: flex;
  margin-top: 60px;
  margin-left: 80px;
  margin-right: 80px; }
  @media (max-width: 424px) {
    nav {
      margin-top: 25px;
      margin-left: 25px;
      margin-right: 25px; } }
  @media (min-width: 1280px) {
    nav {
      width: 1280px;
      margin-left: auto;
      margin-right: auto; } }
  nav > a {
    align-self: center;
